@import url('https://fonts.googleapis.com/css2?family=Shantell+Sans:ital,wght@0,300..800;1,300..800&display=swap');

body {
	display: flex;
	justify-content: center;
	padding: 3px;
}

.active {
	background-color: cyan;
}

.checked {
	box-shadow: rgb(255, 0, 0) 0px 30px 60px -12px inset, rgba(177, 152, 152, 0.219) 0px 18px 36px -18px inset;
}

.black {
	height: 50px;
	width: 50px;
	background-color: #80a352;
}

.black:hover {
	background-color: cyan;
}

.white {
	height: 50px;
	width: 50px;
	background-color: #a8c2b6;
}

.white:hover {
	background-color: cyan;
}

.box_row {
	display: flex;
	flex-direction: row;
	justify-content: center;
	min-width: 100%;
}

.chessboard {
	max-width: 400px;
	max-height: 400px;
	flex-direction: column;
	transform: rotate(270deg);
	margin-top: 10px;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.control_board {
	max-width: 500px;
}

img {
	height: 100%;
	width: 100%;
}

.box {
	font-size: 38px;
	font-weight: 400;
	transform: rotate(90deg);
	display: flex;
	max-width: 111.5%;
	max-height: 11.5%;
	justify-content: center;
	align-items: center;
}

.possible {
	position: relative;
}

.possible::before {
	content: "";
	width: 50%;
	height: 50%;
	border-radius: 100%;
	position: absolute;
	box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.quiz_modal {
	min-height: 100vh;
	min-width: 100vw;
	z-index: 1;
	position: fixed;
	top: 0px;
	left: 0px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.modal_container {
	max-width: 400px;
	min-width: 300px;
	border: 1px solid lightgrey;
	padding: 10px;
	margin: auto;
	border-radius: 10px;
}

.promotion_btn {
	font-size: 45px;
	margin: 2px;
}

.top_logo {
	max-width: 150px;
	margin: auto;
	border-radius: 100%;
	box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.menu_container {
	min-width: 300px;
	padding: 5px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.menu_btn {
	font-size: 25px;
	font-family: "Shantell Sans", cursive;
	box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.control_btn {
	margin: 3px;
	box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}